'use client';

import React from 'react';
import { Typography } from 'antd';

const { Text } = Typography;

interface SwipeChoiceProps {
  type: 'like' | 'nope';
  opacity?: number;
}

export function SwipeChoice({ type, opacity = 1 }: SwipeChoiceProps) {
  const isLike = type === 'like';
  const color = isLike ? '#52c41a' : '#ff4d4f';
  const bgColor = isLike ? 'rgba(82, 196, 26, 0.1)' : 'rgba(255, 77, 79, 0.1)';

  const containerStyle: React.CSSProperties = {
    borderRadius: 20,
    padding: '12px 24px',
    border: `6px solid ${color}`,
    backgroundColor: 'rgba(255,255,255,0.95)',
    backdropFilter: 'blur(8px)',
    transform: isLike ? 'rotate(-20deg)' : 'rotate(20deg)',
    boxShadow: `0 8px 32px ${color}40, 0 4px 16px rgba(0,0,0,0.2)`,
    opacity,
    minWidth: 120,
    textAlign: 'center' as const
  };

  const textStyle: React.CSSProperties = {
    color,
    fontSize: 32,
    fontWeight: 900,
    textTransform: 'uppercase' as const,
    letterSpacing: '3px',
    margin: 0,
    textShadow: `2px 2px 4px ${color}20`,
    lineHeight: 1
  };

  const iconStyle: React.CSSProperties = {
    fontSize: 24,
    marginRight: 8,
    color
  };

  return (
    <div style={containerStyle}>
      <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
        {isLike ? (
          <span style={iconStyle}></span>
        ) : (
          <span style={iconStyle}></span>
        )}
        <Text style={textStyle}>
          {isLike ? 'LIKE' : 'NOPE'}
        </Text>
      </div>
    </div>
  );
}