'use client';

import React, { useCallback } from 'react';
import { Button, Flex, Tooltip } from 'antd';
import { 
  UndoOutlined, 
  CloseOutlined, 
  HeartFilled, 
  StarFilled, 
  ThunderboltFilled,
  ReloadOutlined
} from '@ant-design/icons';
import { UserProfile } from '@/data/swipeData';

interface SwipeFooterProps {
  onChoice: (choice: number) => void;
  onUndo: () => void;
  canUndo: boolean;
  topCard?: UserProfile;
}

const COLORS = {
  like: '#52c41a',
  nope: '#ff4d4f', 
  star: '#1890ff',
  undo: '#8c8c8c',
  boost: '#722ed1'
};

export function SwipeFooter({ onChoice, onUndo, canUndo, topCard }: SwipeFooterProps) {
  const handleReload = useCallback(() => {
    console.log('Reload action');
  }, []);

  const handleFlash = useCallback(() => {
    console.log('Boost action');
  }, []);

  const handleStar = useCallback(() => {
    console.log('Super like action');
    onChoice(1); // Treat as like for now
  }, [onChoice]);

  // Check if current top card is an ad
  const isAd = topCard?.type === 'ad';

  const containerStyle: React.CSSProperties = {
    position: 'absolute',
    bottom: 20,
    left: '50%',
    transform: 'translateX(-50%)',
    zIndex: 1000,
    background: 'linear-gradient(to top, rgba(0,0,0,0.3) 0%, transparent 100%)',
    padding: '16px 24px',
    borderRadius: 40,
    backdropFilter: 'blur(10px)'
  };

  const baseButtonStyle: React.CSSProperties = {
    borderRadius: '50%',
    boxShadow: '0 4px 16px rgba(0,0,0,0.2)',
    transition: 'all 0.2s ease',
    border: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  };

  const smallButtonStyle: React.CSSProperties = {
    ...baseButtonStyle,
    width: 48,
    height: 48
  };

  const largeButtonStyle: React.CSSProperties = {
    ...baseButtonStyle,
    width: 56,
    height: 56
  };

  return (
    <div style={containerStyle}>
      <Flex justify="center" align="center" gap="large">
        {/* Undo Button */}
        <Tooltip title={canUndo ? "Undo last swipe" : "No swipes to undo"} placement="top">
          <Button
            style={{
              ...smallButtonStyle,
              backgroundColor: '#ffffff',
              opacity: canUndo ? 1 : 0.5,
              cursor: canUndo ? 'pointer' : 'not-allowed',
              transform: 'scale(1)',
              transition: 'all 0.2s ease'
            }}
            onClick={onUndo}
            disabled={!canUndo}
            icon={<UndoOutlined style={{ color: COLORS.undo, fontSize: 18 }} />}
            onMouseEnter={(e) => {
              if (canUndo) {
                e.currentTarget.style.transform = 'scale(1.1)';
                e.currentTarget.style.boxShadow = '0 6px 20px rgba(0,0,0,0.3)';
              }
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.transform = 'scale(1)';
              e.currentTarget.style.boxShadow = '0 4px 16px rgba(0,0,0,0.2)';
            }}
          />
        </Tooltip>

        {/* Pass/Nope Button */}
        <Tooltip title="Pass (Swipe Left)" placement="top">
          <Button
            style={{
              ...largeButtonStyle,
              backgroundColor: '#ffffff',
              transform: 'scale(1)',
              transition: 'all 0.2s ease'
            }}
            onClick={() => onChoice(-1)}
            icon={<CloseOutlined style={{ color: COLORS.nope, fontSize: 24, fontWeight: 'bold' }} />}
            onMouseEnter={(e) => {
              e.currentTarget.style.transform = 'scale(1.1)';
              e.currentTarget.style.backgroundColor = COLORS.nope;
              e.currentTarget.querySelector('span')!.style.color = '#ffffff';
              e.currentTarget.style.boxShadow = '0 6px 20px rgba(255,77,79,0.4)';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.transform = 'scale(1)';
              e.currentTarget.style.backgroundColor = '#ffffff';
              e.currentTarget.querySelector('span')!.style.color = COLORS.nope;
              e.currentTarget.style.boxShadow = '0 4px 16px rgba(0,0,0,0.2)';
            }}
          />
        </Tooltip>

        {/* Super Like Button */}
        <Tooltip title={isAd ? "Not available for ads" : "Super Like (Swipe Up)"} placement="top">
          <Button
            style={{
              ...smallButtonStyle,
              backgroundColor: '#ffffff',
              opacity: isAd ? 0.5 : 1,
              cursor: isAd ? 'not-allowed' : 'pointer',
              transform: 'scale(1)',
              transition: 'all 0.2s ease'
            }}
            onClick={handleStar}
            disabled={isAd}
            icon={<StarFilled style={{ color: COLORS.star, fontSize: 18 }} />}
            onMouseEnter={(e) => {
              if (!isAd) {
                e.currentTarget.style.transform = 'scale(1.1)';
                e.currentTarget.style.backgroundColor = COLORS.star;
                e.currentTarget.querySelector('span')!.style.color = '#ffffff';
                e.currentTarget.style.boxShadow = '0 6px 20px rgba(24,144,255,0.4)';
              }
            }}
            onMouseLeave={(e) => {
              if (!isAd) {
                e.currentTarget.style.transform = 'scale(1)';
                e.currentTarget.style.backgroundColor = '#ffffff';
                e.currentTarget.querySelector('span')!.style.color = COLORS.star;
                e.currentTarget.style.boxShadow = '0 4px 16px rgba(0,0,0,0.2)';
              }
            }}
          />
        </Tooltip>

        {/* Like Button */}
        <Tooltip title="Like (Swipe Right)" placement="top">
          <Button
            style={{
              ...largeButtonStyle,
              backgroundColor: COLORS.like,
              transform: 'scale(1)',
              transition: 'all 0.2s ease'
            }}
            onClick={() => onChoice(1)}
            icon={<HeartFilled style={{ color: '#ffffff', fontSize: 24 }} />}
            onMouseEnter={(e) => {
              e.currentTarget.style.transform = 'scale(1.1)';
              e.currentTarget.style.boxShadow = '0 6px 20px rgba(82,196,26,0.4)';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.transform = 'scale(1)';
              e.currentTarget.style.boxShadow = '0 4px 16px rgba(0,0,0,0.2)';
            }}
          />
        </Tooltip>

        {/* Boost Button */}
        <Tooltip title={isAd ? "Not available for ads" : "Boost Profile"} placement="top">
          <Button
            style={{
              ...smallButtonStyle,
              backgroundColor: '#ffffff',
              opacity: isAd ? 0.5 : 1,
              cursor: isAd ? 'not-allowed' : 'pointer',
              transform: 'scale(1)',
              transition: 'all 0.2s ease'
            }}
            onClick={handleFlash}
            disabled={isAd}
            icon={<ThunderboltFilled style={{ color: COLORS.boost, fontSize: 18 }} />}
            onMouseEnter={(e) => {
              if (!isAd) {
                e.currentTarget.style.transform = 'scale(1.1)';
                e.currentTarget.style.backgroundColor = COLORS.boost;
                e.currentTarget.querySelector('span')!.style.color = '#ffffff';
                e.currentTarget.style.boxShadow = '0 6px 20px rgba(114,46,209,0.4)';
              }
            }}
            onMouseLeave={(e) => {
              if (!isAd) {
                e.currentTarget.style.transform = 'scale(1)';
                e.currentTarget.style.backgroundColor = '#ffffff';
                e.currentTarget.querySelector('span')!.style.color = COLORS.boost;
                e.currentTarget.style.boxShadow = '0 4px 16px rgba(0,0,0,0.2)';
              }
            }}
          />
        </Tooltip>
      </Flex>
    </div>
  );
}