'use client';

import React from 'react';
import { motion, PanInfo } from 'framer-motion';
import { SwipeCard } from './SwipeCard';
import { SwipeChoice } from './SwipeChoice';
import { UserProfile } from '@/data/swipeData';

interface SwipeCardStackProps {
  profiles: UserProfile[];
  onSwipeComplete: (direction: 'left' | 'right') => void;
  onSwipeProgress?: (x: number, y: number, rotation: number) => void;
}

const SWIPE_THRESHOLD = 80;
const SWIPE_POWER_THRESHOLD = 25000;
const ROTATION_FACTOR = 0.08;
const MAX_ROTATION = 15;

export function SwipeCardStack({
  profiles,
  onSwipeComplete,
  onSwipeProgress,
}: SwipeCardStackProps) {
  const [dragRotation, setDragRotation] = React.useState(0);
  const [dragX, setDragX] = React.useState(0);
  const [dragY, setDragY] = React.useState(0);

  const containerStyle: React.CSSProperties = {
    position: 'relative',
    width: '100%',
    height: '100vh',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    perspective: '1000px',
  };

  const handleDragEnd = (event: any, info: PanInfo) => {
    const swipeVelocity = info.velocity.x;
    const swipeDistance = info.offset.x;
    const swipePower = Math.abs(swipeVelocity) * Math.abs(swipeDistance);

    // More sensitive detection for better UX
    if (swipePower > SWIPE_POWER_THRESHOLD || Math.abs(swipeDistance) > SWIPE_THRESHOLD) {
      const direction = swipeDistance > 0 ? 'right' : 'left';
      
      // Simulate haptic feedback
      if (navigator.vibrate) {
        navigator.vibrate(50);
      }
      
      onSwipeComplete(direction);
    } else {
      // Reset drag state only if not swiping
      setDragRotation(0);
      setDragX(0);
      setDragY(0);
    }
  };

  const handleDrag = (event: any, info: PanInfo) => {
    // Calculate rotation with tilt effect - clockwise for right, counter-clockwise for left
    const rotation = Math.max(-MAX_ROTATION, Math.min(MAX_ROTATION, info.offset.x * ROTATION_FACTOR));
    setDragRotation(rotation);
    setDragX(info.offset.x);
    setDragY(info.offset.y);
    onSwipeProgress?.(info.offset.x, info.offset.y, rotation);
  };

  if (profiles.length === 0) {
    return null;
  }

  return (
    <div style={containerStyle}>
      {profiles.slice(0, 3).map((profile, index) => {
        const isTopCard = index === 0;
        
        return (
          <motion.div
            key={`${profile.id}-${index}`}
            style={{
              position: 'absolute',
              width: 320,
              height: 520,
              borderRadius: 20,
              cursor: isTopCard ? 'grab' : 'default',
              zIndex: profiles.length - index,
            }}
            initial={false}
            animate={{
              scale: isTopCard ? 1 : 0.95 - index * 0.03,
              y: isTopCard ? dragY * 0.1 : index * 6,
              x: isTopCard ? dragX : 0,
              rotateY: isTopCard ? 0 : index * -1.5,
              rotateZ: isTopCard ? dragRotation : 0,
              opacity: index < 3 ? 1 : 0,
              boxShadow: isTopCard 
                ? '0 12px 40px rgba(0, 0, 0, 0.4)' 
                : `0 ${6 + index * 2}px ${20 + index * 4}px rgba(0, 0, 0, ${0.25 - index * 0.05})`,
            }}
            transition={{
              type: "spring",
              stiffness: 400,
              damping: 35,
              mass: 0.8,
            }}
            drag={isTopCard ? true : false}
            dragConstraints={{ left: -300, right: 300, top: -100, bottom: 100 }}
            dragElastic={0.2}
            dragMomentum={false}
            onDrag={isTopCard ? handleDrag : undefined}
            onDragStart={isTopCard ? () => {
              // Haptic feedback on drag start
              if (navigator.vibrate) {
                navigator.vibrate(20);
              }
            } : undefined}
            onDragEnd={isTopCard ? handleDragEnd : undefined}
            whileDrag={{
              cursor: 'grabbing',
              scale: 1.02,
              zIndex: 1000,
              boxShadow: '0 20px 60px rgba(0, 0, 0, 0.5)',
              transition: { duration: 0.1 }
            }}
            whileHover={isTopCard ? {
              scale: 1.01,
              transition: { duration: 0.2 }
            } : undefined}
            exit={{
              x: dragX > 0 ? 600 : -600,
              rotateZ: dragX > 0 ? 30 : -30,
              opacity: 0,
              scale: 0.7,
              y: -100,
              transition: { 
                duration: 0.5,
                ease: [0.25, 0.46, 0.45, 0.94]
              }
            }}
          >
            <SwipeCard profile={profile} />
            
            {/* Choice indicator for top card - will be handled by parent component */}
          </motion.div>
        );
      })}
    </div>
  );
}